import React  from 'react';
import './Login.css'
import {Link, Route, Switch, Redirect } from 'react-router-dom';
import axios from 'axios';
import { useState, useContext } from 'react';
import Cookies from 'js-cookie';
import AuthContext from '../AuthContext';
import { Spinner } from 'react-bootstrap';
function Login(props){
	
	let[ emailModal, emailModal ] = useState(false);
	let[ spinnerModal, spinnerModal] = useState(false);
	const { authToken, setAuthToken } = useContext(AuthContext);
	
	let [userId, userId] = useState(null); //  modal   state
	let [userPw, userPw] = useState(null);
	
	if (authToken){	
		props.loginOrlogout('');
		return <Redirect to="/" />;
	}

    return(
			<>
        <div className='login'>
					<h3> </h3>
					<div className="login-box">
						<form onSubmit={(e)=>{
							e.preventDefault();
							spinnerModal(true);
							axios.post('/login',{
								loginId : e.target.loginId.value,
								password : e.target.password.value
							})
							.then((res)=>{
								if(res.data===200){ //
									console.log(res.status,res.data);
									alert('  ');
									props.loginOrlogout('');
									const authToken = 'fdsojfspodijfosfjho';
									setAuthToken(authToken);
									Cookies.set('token', authToken);
									spinnerModal(false);
								}
								else if(res.data===300){ // 300 ->   
									emailModal(true);
									spinnerModal(false);
									userId(e.target.loginId.value);
									userPw(e.target.password.value);
									alert('  ');
								} 
								else if(res.data===412){//
									console.log(res.status,res.data);
									alert(' ');
									spinnerModal(false);
								}
							})
							.catch((err)=>{
								console.log(err);
							})
						}}>
							<div className='id-container'>
								<div className="id"></div>
								<input type="text" name="loginId"/>
							</div>
							<div className='pw-container'>
								<div className="pw"></div>
								<input type="password" name="password" />
							</div>
							<div className="login-button">
								<input type='submit' value='Login'/>
							</div>
						</form>
						{spinnerModal===true?<SpinnerModal />:null}
					</div>

					<Link to='/join' className='join-box'>
							<p></p>
					</Link>
				
        </div>
				{ emailModal===true? <EmailModal userId={userId} userPw={userPw}  />:null}
			</>

    );
}

function EmailModal(props){
	let [spinner,spinner] = useState(false);
	return(
		<div className="emailModal-top">
			<form onSubmit={(e)=>{
				e.preventDefault();
				spinner(true);
				axios.post('/join/mail',{
					authenticationKey : e.target.authenticationKey.value,
					loginId : props.userId,
					password : props.userPw,
				})
				.then((res)=>{
					if(res.data===200){ //  
						spinner(false);
						alert(' ');
					}
					else if(res.data===412){ //  
						spinner(false);
						alert(' ');
					}
				})
				.catch((err)=>{
					console.log(err)
				})
			}}>
				<p className="emailModal-context">
					  
				</p>
				<input type="text" name="authenticationKey"/>
				<input type="submit" value="" />
			</form>
			{
			spinner===true
			?<Spinner animation="grow" variant="light" />
			:null
			}
		</div>
	);
}

function SpinnerModal(){
	return(
		<div className='spinner-container'>	
			<Spinner animation="border" className="spinner"/>
		</div>
	)
}

export default Login